<template>
  	<!-- Input personalizado -->
	<div class="vr_input">
		<fieldset>
			<legend>{{ titulo }}</legend>

			<div class="apresentacao_img">
        <i class="fas fa-upload" :id="'upload_icon_'+id"/>
				<img src='' onerror="this.style.display='none'" :id="'imagem_ref_'+id">
        <p :id="'show_img_b64_'+id" style="display:none"></p>
			</div>

			<div class="input_img">
				<input type="file" :id="'input_file_img_'+id" :required=required @change="iniciaProcessoUpload(id)">
          <p>Tamanho Limite da Imagem <span :id="'height_limite_'+id">{{ height_limite }}</span>x<span :id="'width_limite_'+id">{{ width_limite }}</span></p>
			</div>
		</fieldset>
	</div>
</template>

<script>
  export default {
    
    props: ['titulo', 'id', 'required', "height_limite", "width_limite"],

    methods: {
      // Step Inicial para o processo de registro da Imagem
      iniciaProcessoUpload(id) {
        // Seleciona a imagem subido pelo usurio
        let input = document.getElementById(`input_file_img_${id}`);
        let file = input.files[0];

        // Obtem a extenso do file
        let extensao = file.name.split('.').pop();

        // Pega os limites estabelecidos pela aplicao
        let height_limite = parseInt($(`#height_limite_${id}`).html());
        let width_limite  = parseInt($(`#width_limite_${id}`).html());

        // Apenas ir sumir com o incone de Upload
        $(`#upload_icon_${id}`).css('display', 'none');

        try {
          if (extensao != "jpg")
            throw new Error("Extenso invlida da imagem!"); 
            
          // Processo de upload da imagem ao input file e ao 'src' respectivo
          let reader = new FileReader();

          console.log("Test");

          reader.onload = function(e) {
            $(`#imagem_ref_${id}`).attr("src", e.target.result);
            $(`#show_img_b64_${id}`).html(e.target.result);
            $(`#imagem_ref_${id}`).css("display", "block");
          };

          reader.onerror = function(error) {
            alert(error);
          };
          reader.readAsDataURL(file);

          // Validaes dos tamanhos e regras da imagem
          this.isImagemValida(id, height_limite, width_limite, file)
        } 
        catch (error) {
          this.resetCamposUpload();
        }

      },

      // Ir iniciar o processo de validaes da imagem selecionada
      isImagemValida(id, height_limite, width_limite, imagem) {
        let _URL = window.URL;
        let img;
        var vueJS = this;
        
        img = new Image();
        img.onload = function () {
          try {
            console.log(this.height , height_limite ,this.width, width_limite)
            if ((this.height != height_limite) || (this.width != width_limite)) 
              throw new Error("A imagem selecionada no atende as dimenses especificadas!"); // Dispara um Erro para matar toda a pilha de processos.
          }
          catch (error) {
            alert("A imagem selecionada no atende as dimenses especificadas!");
            vueJS.resetCamposUpload(id);
          }
        };

        img.src = _URL.createObjectURL(imagem);
      },

      // Reseta todas as informaes da imagem
      resetCamposUpload(id) {
        // Campos Principais para requisio
        $(`#upload_icon_${id}`).css('display', 'block');
        $(`#imagem_ref_${id}`).attr("src", "");
        $(`#show_img_b64_${id}`).html("");
        $(`#input_file_img_${id}`).val("");
      }


    }
  }
</script>

<style scoped>
 fieldset {
    display: block;
    margin-left: 2px;
    margin-right: 2px;
    padding-bottom: 0.725em;
    padding-left: 0.75em;
    padding-right: 0.75em;
    border: 1px solid var(--borda-input);
    border-radius: 0.25rem;
  }

  legend {
    display: block;
    width: auto;
    max-width: 100%;
    padding-left: 0.3rem;
    padding-right: 0.35rem;
    margin-bottom: .5rem;
    font-size: 1rem;
    line-height: inherit;
    color: inherit;
    white-space: normal;
    color: var(--borda-input);
  }

	.vr_input > fieldset {
    font-size: 1rem;
  }

	.input_img {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	.input_img > input {
    border: none;
    outline: none;
    font-weight: bold;
  }

	.input_img > p, .input_img > span {
    font-weight: bold;
  }

	.apresentacao_img {
		display: flex;
		align-content: center;
		justify-content: center;
	}

	.apresentacao_img > img {
		width: auto;
	}

</style>


