<template>
  <div class="fundo_modal">
    <div class="my_modal">
      <div class="modal_header">
        <span class="title">Mais informaes</span>
        <hr />
      </div>
      <div class="modal_body">
        <form class="form">
					<div class="flex">
						<inputDefault type="text" titulo="Endereo" id="endereco" required="true"/>

						<inputDefault type="text" titulo="Merchant ID" id="id" required="true"/>

						<inputDefault type="text" titulo="Merchant KEY" id="key" required="true"/>
					</div>
					<div class="btn_submit">
						<b-button variant="danger" @click="closeModal">Cancelar</b-button>
						<b-button type="submit" variant="success">Confirmar</b-button>
					</div>
				</form>

      </div>
    </div>
  </div>
</template>

<script>

  import InputDefault from '../../input/InputDefault.vue';
 
	export default {
		components: {
      inputDefault: InputDefault
    },

		methods: {
			// Fecha o modal
			closeModal() {
				$(`.fundo_modal`).css( "display", "none");
			}
		}
	}
</script>

<style scoped>

  .modal_header > span {
		text-align: center;
    font-weight: bold;
  }

	.modal_body {
		margin-top: 1rem;
	}

	.flex {
		display: flex;
		flex-direction: column;
		gap: 1rem;
	}

  .btn_submit {
    margin-top: 2rem;
    float: right;
  }

</style>


