<template>
  <div>

    <div class="grid_two_logo logo_cliente">
        <inputImageLogo
        titulo="Logo do Cliente"
        id="img_logo"
        required="true"
        />
        <span>
        <b-button type="submit" variant="success">Salvar</b-button>
        </span>
    </div>

    <div class="grid_two">
        <inputDefault
        type="text"
        titulo="Nome do supermercado"
        id="nome_supermercado"
        required="true"
        />
        <inputDefault
        type="text"
        titulo="Poltica de privacidade"
        id="politica_privacidade"
        required="true"
        />
    </div>

    <div class="grid_two">
        <inputDefault
        type="text"
        titulo="Login Dashboard"
        id="login_dash"
        required="true"
        />
        <inputDefault
        type="text"
        titulo="Senha Dashboard"
        id="senha_dash"
        required="true"
        />
    </div>

    <div class="grid_two">
        <inputColor
        titulo="Cor primria"
        id="cor_primaria"
        />
        <inputColor
        titulo="Cor secundria"
        id="cor_secundaria"
        />
    </div>

    <div class="grid_two">
        <inputDefault
        type="text"
        titulo="QR Code"
        id="qr_code"
        required="true"
        />
        <inputDefault
        type="text"
        titulo="Email chave"
        id="email_chave"
        required="true"
        />
    </div>

    <div class="grid_one">
        <inputDefault type="text" titulo="Token" id="token" required="true" />
    </div>

    <div class="grid_two">
        <inputImageComView
        titulo="Imagem do email"
        id="img_email"
        required="true"
        width_limite="125"
        height_limite="110"
        />
        <inputImageComView
        titulo="Imagem de compartilhamento"
        id="img_compartilhamento"
        required="true"
        width_limite="256"
        height_limite="256"
        />
    </div>
	</div>
</template>

<script>
	import InputDefault      from "../../components/shared/input/InputDefault.vue";
	import InputImageComView from "../../components/shared/input/InputImageComView.vue";
	import InputImageLogo    from "../../components/shared/input/InputImageLogo.vue";
	import InputColor        from "../../components/shared/input/InputColor.vue";

	export default {
		components: {
			inputDefault: InputDefault,
			inputImageComView: InputImageComView,
			inputImageLogo: InputImageLogo,
			inputColor: InputColor,
		},
	}

</script>

<style scoped>
  .grid_two_logo {
    display: grid;
    grid-template-columns: 5fr 1fr;
    gap: 2rem;
    margin-bottom: 1rem;
    align-items: center;

  }

  .grid_two_logo > div {
    grid-column: 1/2;
  }

  .grid_two_logo > span {
    grid-column: 2/3;
    display: flex;
  }

  .grid_two_logo > span > button {
    width: 100%;
  }
</style>


