<template>
  <div class="fundo_modal">
    <div class="my_modal">
      <span class="close_modal" @click="closeModal">X</span>
      <div class="modal_header">
        <span class="title">Selecione os produtos desse cliente</span>
        <hr />
        <p>De quais produtos ou recusos o seu aplicativo precisa?</p>
      </div>
      <div class="modal_body">
        <form ref="form" @submit.stop.prevent="handleSubmit">
          
          <inputDefault type="text" titulo="Nome do supermercado" id="nome_supermercado" required="true"/>

          <div class="card_sistemas">
            <div               
              v-for="sistema of sistemas" 
              :key="sistema.id" 
              class="sistema"
            >
              <div class="card_info_sistema" @click="checkSistema(sistema.id, sistema.titulo)">
                <img :src="sistema.imagem" :alt="sistema.titulo">
                <span>{{ sistema.titulo }}</span>
                <p>{{ sistema.descricao }}</p>
              </div>

              <input type="checkbox" name="sistema[]" :value="sistema.id" :id=sistema.titulo>
              <label :for="sistema.titulo" />

            </div>
          </div>

          <div class="btn_submit">
            <b-button variant="danger" @click="closeModal">Cancelar</b-button>
            <b-button type="submit" variant="success">Confirmar</b-button>
          </div>

        </form>
      </div>
    </div>
  </div>
</template>

<script>
  import InputDefault from '../../input/InputDefault.vue';
  
  export default {

    components: {
      inputDefault: InputDefault
    },

    data() {
      return {
        stateNome: null,
        nomeNovoCliente: "",
        selected: [], // Must be an array reference!
        sistemas: [
          { id: 1, imagem: "/image/emb_android.png",   titulo: 'Android',    descricao: 'Aparelhos com sistema operacional Android podem baixar este aplicativo e utilizar em seu aparelho' },
          { id: 2, imagem: "/image/emb_apple.png",     titulo: 'iOS',        descricao: 'Aparelhos com sistema operacional iOS podem baixar este aplicativo e utilizar em seu aparelho' },
          { id: 3, imagem: "/image/emb_ecommerce.png", titulo: 'E-commerce', descricao: 'Um site personalizado onde os clientes podem comprar seus produtos sem sair de casa' },
          { id: 4, imagem: "/image/emb_android.png",   titulo: 'M-commerce', descricao: 'Um aplicativo personalizado onde os clientes podem comprar seus produtos sem sair de casa' },
          { id: 5, imagem: "/image/emb_android.png",   titulo: 'VRMidia',   descricao: 'Passe as suas ofertas na TV de seu supermercado' }
        ]
      }
    },

    methods: {
      // Verifica Campos 
      checkFormValidity() {
        const valid    = this.$refs.form.checkValidity()
        this.stateNome = valid
        return valid
      },
      // Reseta o modal
      resetModal() {
        this.nomeNovoCliente = ''
        this.stateNome       = null
        // Reset Input
        $("#nome_supermercado").val("");
        // resetando os selecionados
        let e_checkbox = $("input:checked");

        for (let i = 0, l = e_checkbox.length; i < l; i++)
          e_checkbox[i].checked = false;

      },
      // Cancela o envio do Form
      handleCadastro(bvModalEvt) {
        // Prevent modal from closing
        bvModalEvt.preventDefault()
        // Trigger submit handler
        this.handleSubmit()
      },
      // Dispara as validaes e cadastro
      handleSubmit() {
        // Validaes
        if (!this.checkFormValidity()) {
          return
        }

        // Quando tudo estiver ok, aqui haver uma requisio para a API, onde a mesma
        // deve retornar o ID do Cliente cadastrado

        this.$router.push({ path: `cliente/${123}` })
      },
      // Realiza o check ao clicar dentro da div dos Sistemas
      checkSistema(id, titulo) {
        if($(`#${titulo}`).prop( "checked"))
          $(`#${titulo}`).prop( "checked", false);
        else
          $(`#${titulo}`).prop( "checked", true);
      },
      // Fecha o modal
      closeModal() {
        this.resetModal();
        $(`.fundo_modal`).css( "display", "none");
      }
    }
  };
</script>

<style scoped>

  input[type=checkbox] {
    display: none;
  }

  input[type=checkbox]+label {
    background-color: #afafaf;
    border-radius: 0.5rem;
    display: inline-block;
    height: 1rem;
    width: 1rem;
    margin: 0 8px;
  }

  input[type=checkbox]:checked+label {
    background-color: var(--selecionado);
  }

  .close_modal {
    position: absolute;
    top: 1;
    right: 0;
    margin-right: 1rem;
    cursor: pointer;
  }

  .modal_header > span {
    font-weight: bold;
  }

	.modal_header > p {
		margin: 1.5rem 0 1.5rem 0;
	}

  .card_sistemas {
    margin-top: 1rem;
  }

  .sistema {
    display: flex;
    justify-content: space-between;
    margin-top: 0.5rem;
    border: 3px solid #f0f0f0;
    border-radius: 0.25rem;
    padding: 0.3rem;
  }

  .card_info_sistema {
    display: grid;
    grid-template-rows: auto;
    grid-template-columns: 3.5rem auto;
    gap: 0.2rem;
    padding: 0.5rem 0;
    cursor: pointer;
    width: 100%;
    align-items: center;
    -webkit-user-select: none; /* Safari */        
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
  }

  .card_info_sistema > img {
    grid-row: 1/3;
    grid-column: 1/2;
    height: 3rem;
  }

  .card_info_sistema > span {
    display: block;
    grid-row: 1/2;
    grid-column: 2/3;
    font-weight: bold;
  }

  .card_info_sistema > p {
    display: block;
    grid-row: 2/3;
    grid-column: 2/3;
    text-align: justify;
    text-justify: inter-word;
    margin-right: 1rem;
  }

  .btn_submit {
    margin-top: 2rem;
    float: right;
  }

</style>


