<template>
  <div class="main">
    <!-- NavBar -->
    <navBar />
    <!-- Modal novo Cliente -->
    <modalNovoCliente id="modal_novo_cliente"/>
    <!-- Menu -->
    <myMenu />
    <!-- Conteudo dinamico -->
    <div class="conteudo">

      <!-- Titulo da Pgina -->
      <div class="conteudo_title">
        <h2>{{ titulo }}</h2>
          <div class="conteudo_search"> 
            <!-- Input p/ filtro dos Clientes -->
            <inputSearch>
              <input v-on:input="filtro = $event.target.value" type="search" placeholder="Pesquise pelo nome do cliente" />
            </inputSearch>

            <b-button @click="showModal" class="btn_add_cliente">Adicionar Cliente</b-button>
          </div>
      </div>

      <!-- Card dos Clientes -->
      <div id="cards" class="cards_clientes_grid">
        <card 
          v-for="card of cardsClientesFiltrados" 
          :key="card.id" 
          :id="card.id"
          :nome_cliente="card.nome_cliente" 
          :grupo="card.grupo_cliente" 
          :modo="card.modo" 
          :apple="card.apple" 
          :v_apple="card.versao_apple" 
          :android="card.android" 
          :v_android="card.versao_android" 
          :ecommerce="card.ecommerce" 
          :v_ecommerce="card.versao_ecommerce" 
          :dash="card.dash" 
          :v_dash="card.versao_dash" 
          :midia="card.midia" 
          :v_midia="card.versao_midia" 
        />
      </div>

    </div>
  </div>
</template>

<script>
  // Libs 
  import axios            from 'axios';
  // Components
  import NavBar           from '../components/shared/navbar/NavBar.vue';
  import Menu             from '../components/shared/menu/Menu.vue';
  import CardHome         from '../components/shared/card/CardHome.vue';
  import NovoClienteModal from '../components/shared/modal/cliente/NovoClienteModal.vue';
  import InputSearch      from '../components/shared/input/InputSearch.vue';

  export default {

    components: {
      navBar: NavBar,
      myMenu: Menu,
      card: CardHome,
      modalNovoCliente: NovoClienteModal,
      inputSearch: InputSearch,
    },

    data() {
      return {
        titulo: 'Clientes',
        cards: [],
        filtro: '',
      }
    },

    methods: {
      showModal() {
        $("#modal_novo_cliente").css("display", "flex" );
        $("#nome_supermercado").focus();
      }
    },

    computed: {
      cardsClientesFiltrados() {
        if (this.filtro) {
          // criando uma expresso com o valor do filtro, insensitivo
          let exp = new RegExp(this.filtro.trim(), 'i');
          return this.cards.filter(card => exp.test(card.nome_cliente));
        } 
        else {
          // se o campo estiver vazio, no filtramos, retornamos a lista
          return this.cards;
        }
      }
    },

    created() {

      // let request = axios.create({
      //   baseURL: `https://dev.nweb.com.br/api-homologacao/v2/sgc/a0.01/`,
      //   withCredentials: false,
      //   headers: {
      //       "Authorization": "Bearer "+"so6GOYrt6RVZbS5HgGio7QFwebZZ8ejZFg094WxGnaC7BFxTJtFWjetqkROXYyS854A4AMBBv80AgeiAOe9Y6A6ZYzy1bf4nJtlg",
      //       "Access-Control-Allow-Origin": "*",
      //       "Access-Control-Allow-Methods": "GET, POST, OPTIONS, PUT, DELETE",
      //       "Access-Control-Allow-Headers": "Origin, Content-Type, Authorization",
      //       "Content-Type": "application/json",
      //   }
      // })

      // request
      // .get('/gerenciaLojas')
      // .then(response => (console.log(response)))
      // .catch(error => console.log(error))

      for (var i = 0; i < 40; i++) {
        this.cards[i] = {
          id              : (i + 1),            
          nome_cliente    : "Cliente " + (i+1),
          grupo_cliente   : "Grupo "   + (i+1),
          modo            : Math.floor(Math.random() * 10) >= 5 ? "Ativo" : "Inativo",
          apple           : (Math.floor(Math.random() * 3)),
          versao_apple    : (Math.floor(Math.random() * 2) + "." + Math.floor(Math.random() * 10) + "." + Math.floor(Math.random() * 10)),
          android         : (Math.floor(Math.random() * 3)),
          versao_android  : (Math.floor(Math.random() * 2) + "." + Math.floor(Math.random() * 10) + "." + Math.floor(Math.random() * 10)),
          ecommerce       : (Math.floor(Math.random() * 3)),
          versao_ecommerce: (Math.floor(Math.random() * 2) + "." + Math.floor(Math.random() * 10) + "." + Math.floor(Math.random() * 10)),
          dash            : (Math.floor(Math.random() * 3)),
          versao_dash     : (Math.floor(Math.random() * 3) + "." + Math.floor(Math.random() * 10) + "." + Math.floor(Math.random() * 10)),
          midia           : (Math.floor(Math.random() * 3)),
          versao_midia    : (Math.floor(Math.random() * 2) + "." + Math.floor(Math.random() * 10) + "." + Math.floor(Math.random() * 10))
        }
      }
      
    }

  }

</script>

<style scoped>

  /* Modal */
  #modal_novo_cliente {
    display: none;
  }

  /*  Conteudo Home */

  .conteudo_search {
    display: grid;
    grid-template-columns: 4fr 1fr;
    gap: 1.5rem;
  }

  .icon {
    margin-left: 9px;
    margin-top: 8px;
  }

  .btn_add_cliente {
    padding: 0 1rem;
    background-color: var(--primary);
    color: var(--texto-menu);
    transition: filter 0.5;
  }

  .btn_add_cliente:hover {
    cursor: pointer;
    filter: brightness(0.9);
  } 

  .cards_clientes_grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
  }


</style>



