<template>
  <div class="card" :id="'card_'+id">
		<div class="card_detalhes" v-bind:class="{loja_inativa: !situacao}" @click="showModal('detalhes')">
			<img  :src="img" alt="supermercado">
			<p>SPN {{ nome }}</p>
			<p>{{ cnpj }}</p>
			<p>ID: {{ id }}</p>
		</div>

		<div class="card_footer">
			<!-- Se o cliente estiver ativo -->
			<b-button v-if="situacao" variant="dark">Chave</b-button>
			<b-button v-if="situacao" variant="danger" @click="showModal('cancelar')">Cancelar</b-button>

			<!-- Se o cliente estiver inativo -->
			<b-button 
				v-if="!situacao"
				class="class_btn_reativa_inativo"  
				variant="success"
			>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        Reativar
			</b-button>
			
			<b-button 
				v-if="!situacao" 
				class="class_btn_detalhes_inativo" 
				variant="dark"
			>
				<i class="fas fa-clipboard-list"></i>
			</b-button>

		</div>
  </div>
</template>

<script>

	export default {	
		props: ['id', 'img', "nome", "cnpj", "situacao"],

		data() {
			return {
				ativo: this.situacao,
			};
		},

		methods: {
			showModal(acao) {
				switch (acao) {

					case "detalhes":
						$("#modal_info_loja").css("display", "flex");
					break;
				
					case "cancelar":
						$("#modal_cancela_loja").css("display", "flex");
					break;
				}
			}
		}

	}
</script>

<style scoped>
  @import "../../../assets/css/efeitos/bordaInfinita.css";

	.card {
		display: grid;
		grid-template-rows: 3fr auto;
		justify-content: center;
		gap: 1rem;
		border: 1px solid var(--borda-cards);
		padding: 0.5rem;
		border-radius: 0.25rem;
	}

	.card:hover {
    cursor: pointer;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  }

  .loja_inativa {
    filter: grayscale(1);
    transition: filter 0.85s;
  }

  .loja_inativa:hover  {
    filter: grayscale(0.2);
  }

	.card_detalhes {
		grid-row: 1/2;
		display: flex;
		flex-direction: column;
		gap: 0.5rem;
	}

	.card_detalhes > img {
		width: 100%;
		/* width: 300px;
		height: 400px; */
	} 

	.card_footer {
		grid-row: 2/3;
		display: flex;
		justify-content: space-between;
		align-items: flex-end;

		padding-top: 0.5rem;
		border-top: 1px solid var(--linha-clara);
	}

	.card_footer > button {
		width: 45%;
	}

	.class_btn_reativa_inativo {
		width: 70% !important;
		filter: none !important;
	}

	.class_btn_detalhes_inativo {
		width: 20% !important;
	}

</style>


