<template>
  <div class="card_cliente" v-bind:class="situacaoCliente(modo)" @click="openClient(id)">
    <div class="card_cliente_body">
      <img src="https://picsum.photos/400/400/?image=20" alt="">
      <div class="card_cliente_info">
        <span class="title">{{ nome_cliente }}</span>
        <div class="card_cliente_detalhes">
          <p>{{ grupo }}</p>
          <p>Modo: {{ modo }}</p>
        </div>
      </div>
      <div class="card_sistemas">
        <i class="fab fa-apple"           v-bind:class="situacaoClienteSistema(android)"    :title="v_android"   />
        <i class="fab fa-android"         v-bind:class="situacaoClienteSistema(apple)"      :title="v_apple"     />
        <i class="fas fa-shopping-basket" v-bind:class="situacaoClienteSistema(ecommerce)"  :title="v_ecommerce" />
        <i class="fas fa-chart-bar"       v-bind:class="situacaoClienteSistema(dash)"       :title="v_dash"      />
        <i class="fas fa-tv"              v-bind:class="situacaoClienteSistema(midia)"      :title="v_midia"     />
      </div>
    </div>
  </div>

</template>

<script>

  export default {
    props: ['id', 'nome_cliente', "grupo", "modo", "android", "v_android", "apple", "v_apple", "ecommerce", "v_ecommerce", "dash", "v_dash", "midia", "v_midia"],

    methods: {
      situacaoCliente(situacao) {
        if (situacao == "Ativo")
          return "cliente_ativo"
        
        return "cliente_inativo"
      },
      situacaoClienteSistema(situacao) {
        let r_situacao = "";

        switch (situacao) {
          case (1):
            r_situacao = "ativo";
          break;
        
          case 2:
            r_situacao = "inativo";
          break;

          case 0:
            r_situacao = "";
          break;
        }

        return r_situacao;
      },
      openClient(id) {
        this.$router.push({ path: `cliente/${id}` })
      }
    }
  }

</script>

<style scoped>

  .card_cliente {
    background-color: white;
    min-width: calc(20% - 1.5rem);
    margin: 0.2rem;
    border: 1px solid rgba(160, 160, 160, 0.377);
  }

  .card_cliente {
    transition: filter 0.5s;
  }

  .card_cliente:hover {
    cursor: pointer;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  }

  .card_cliente:active {
    cursor: pointer;
    filter: brightness(0.9);
  }

  .card_cliente_body {
    display: grid;
    grid-template-rows: 3fr 1fr;
    grid-template-columns: 1fr 2fr;
  }

  .card_cliente_body > img {
    grid-row: 1/2;
    grid-column: 1/2;
    width: 100%;
  }

  .card_cliente_info {
    grid-row: 1/2;
    grid-column: 2/3;
    padding: 0 0.5rem 0 0.2rem;
  }

  .card_cliente_info > .title {
    font-weight: bold;
  }

  .card_cliente_detalhes > p {
    margin-top: 1rem;
  }

  .card_cli_image {
    display: flex;
  }

  .card_sistemas {
    grid-row: 2/3;
    grid-column: 1/3;
    display: flex;
    border-top: 1px solid var(--linha-clara);
    justify-content: initial;
    padding: 0.5rem 0.1rem;
  }

  .card_sistemas > i {
    margin: 0rem 0.3rem;
    cursor: help;
  }

  .ativo {
    color: var(--ativo);
  }

  .inativo {
    color: var(--inativo);
  }

  .cliente_inativo {
    transition: filter 0.8s;
    filter: grayscale(1);
  }

  .cliente_inativo:hover {
    filter: grayscale(0);
  }

</style>


