<template>
  <div class="fundo_modal">
    <div class="my_modal">
      <div class="modal_header">
        <span class="title">Adicionar uma nova loja</span>
				<hr />
      </div>
      <div class="modal_body">
        <form>
					<inputDefault type="text" titulo="CNPJ" id="cnpj" required="true"/>

					<div class="btn_submit">
						<b-button variant="danger" @click="closeModal">Cancelar</b-button>
						<b-button type="submit" variant="success">Confirmar</b-button>
					</div>

				</form>
      </div>
    </div>
  </div>
</template>

<script>
  import InputDefault from '../../input/InputDefault.vue';
 
	export default {
		components: {
      inputDefault: InputDefault
    },

		methods: {
			// Fecha o modal
			closeModal() {
				$(`.fundo_modal`).css( "display", "none");
			},

		},

		created() {
			$(document).ready(function(){	
				$("#cnpj").mask("99.999.999/9999-99");
			});
		}
	}
</script>

<style scoped>

  .modal_header > span {
		text-align: center;
    font-weight: bold;
  }

	.modal_header > p {
		margin: 1.5rem 0 1.5rem 0;
	}

	.modal_body {
		margin-top: 2rem;
	}

  .btn_submit {
    margin-top: 2rem;
    float: right;
  }

</style>


