<template>
  <div>

    <div class="header_lojas">
      <!-- Input p/ filtro das Lojas -->
      <inputSearch>
        <input v-on:input="filtro = $event.target.value" type="search" placeholder="Pesquise pelo nome da loja" />
      </inputSearch>

      <b-button class="btn_add_loja" @click="showModal()">Adicionar Loja</b-button>
    </div>
    
    <div class="cards_lojas_grid">
      <card 
        v-for="loja of cardsLojasFiltradosAtivas" 
        :key="loja.id" 
        :id="loja.id"
        :img="loja.img" 
        :nome="loja.nome" 
        :cnpj="loja.cnpj" 
        :situacao="loja.situacao" 
      />
    </div>

    <h4 class="title">Cancelados <hr size="50"></h4>

    <div class="cards_lojas_grid">
      <card 
        v-for="loja of cardsLojasFiltradosInativas" 
        :key="loja.id" 
        :id="loja.id"
        :img="loja.img" 
        :nome="loja.nome" 
        :cnpj="loja.cnpj" 
        :situacao="loja.situacao" 
      />
    </div>

  </div>
</template>

<script>
  import InputSearch from "../../components/shared/input/InputSearch.vue";
  import CardLoja    from "../../components/shared/card/CardLoja.vue";

  export default {

    data() {
      return {
        lojas: [],
        lojasAtivas:   [],
        lojasInativas: [],
        filtro: '',
      }
    },

    components: {
      inputSearch: InputSearch,
      card: CardLoja
    },

    computed: {
      cardsLojasFiltradosAtivas() {
        if (this.filtro) {
          // criando uma expresso com o valor do filtro, insensitivo
          let exp = new RegExp(this.filtro.trim(), 'i');
          return this.lojasAtivas.filter(loja => exp.test(loja.nome));
        } 
        else {
          // se o campo estiver vazio, no filtramos, retornamos a lista
          return this.lojasAtivas;
        }
      },

      cardsLojasFiltradosInativas() {
        if (this.filtro) {
          // criando uma expresso com o valor do filtro, insensitivo
          let exp = new RegExp(this.filtro.trim(), 'i');
          return this.lojasInativas.filter(loja => exp.test(loja.nome));
        } 
        else {
          // se o campo estiver vazio, no filtramos, retornamos a lista
          return this.lojasInativas;
        }
      }
    },

    methods: {
      showModal() {
				$("#modal_nova_loja").css("display", "flex");
				$("#cnpj").focus();
			}
    },

    created() {

      for (let i = 0; i < 15; i++) {
        this.lojas.push({id: i, nome: `Bom Mix Loja ${i}`, img: "https://lh5.googleusercontent.com/p/AF1QipMAp-c01kHcLEwO8KGmc235XtGM1iTC_b0hqELV=w1080-k-no", cnpj: "00.000.000/0000-00", situacao: Math.floor(Math.random() * 10) >= 5 ? true : false});        
      }

      this.lojas.forEach(loja => {
        
        if (loja.situacao) 
          this.lojasAtivas.push(loja);
        else
          this.lojasInativas.push(loja);
        
      });
    }
  }

</script>

<style scoped>

  .header_lojas {
    display: grid;
    grid-template-columns: 80% auto;
  }

  .btn_add_loja {
    padding: 0 1rem;
    background-color: var(--primary);
    color: var(--texto-menu);
    transition: filter 0.5;
  }

  .input_pesquisa {
    width: 20rem;
    margin-right: 2rem;
    padding-left: 0.3rem;
    border: 1px solid #dcdcdc;
    outline: none;
  }

  .cards_lojas_grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    gap: 1rem;
    margin: 2rem 0;
  }

  .title {
    color: var(--primary);
  }

</style>


