<template>
  <div class="main">
    <!-- NavBar -->
    <navBar />
    <!-- Menu -->
    <myMenu />

    <!-- Model info loja -->
    <modalDetalhesLoja id="modal_info_loja"    />
    <modalNovaLoja     id="modal_nova_loja"    />
    <modalCancelaLoja  id="modal_cancela_loja" />

    <div class="conteudo">
      <!-- Navegao -->
      <navegacaoHeader>
        <li id="info"      @click="menuSelecionado('info')"      class="navegacao_item selected">Informaes gerais</li>
        <li id="lojas"     @click="menuSelecionado('lojas')"     class="navegacao_item">Lojas</li>
        <li id="android"   @click="menuSelecionado('android')"   class="navegacao_item">Android</li>
        <li id="ios"       @click="menuSelecionado('ios')"       class="navegacao_item">iOS</li>
        <li id="mcommerce" @click="menuSelecionado('mcommerce')" class="navegacao_item">M-commerce</li>
        <li id="ecommerce" @click="menuSelecionado('ecommerce')" class="navegacao_item">E-commerce</li>
        <li id="vrmidia"   @click="menuSelecionado('vrmidia')"   class="navegacao_item">VR Midia</li>
      </navegacaoHeader>


      <!-- Conteudos dinamicos atrves das opes no navegacaoHeader -->
      <div class="conteudo_dinamico">
        <infoGeraisCliente 
          v-if="showInfo"
        />
        <lojasCliente 
          v-if="showLoja"
        />
      </div>

    </div>
  </div>
</template>

<script>

// Components
import NavBar            from "../components/shared/navbar/NavBar.vue";
import Menu              from "../components/shared/menu/Menu.vue";
import NavegacaoHeader   from "../components/shared/navegacao/NavegacaoHeader.vue";
import ModalDetalhesLoja from "../components/shared/modal/loja/ModalDetalhesLoja.vue";
import ModalNovaLoja     from "../components/shared/modal/loja/ModalNovaLoja.vue";
import ModalCancelaLoja  from "../components/shared/modal/loja/ModalCancelaLoja.vue";

// Telas
import InfoGeraisCliente from "./navegacao/InfoGeraisCliente.vue";
import LojasCliente      from "./navegacao/LojasCliente.vue";

export default {
  data() {
    return {
      showInfo: true,
      showLoja: false
    };
  },

  components: {
    navBar: NavBar,
    myMenu: Menu,
    navegacaoHeader: NavegacaoHeader,
    infoGeraisCliente: InfoGeraisCliente,
    lojasCliente: LojasCliente,
    modalDetalhesLoja: ModalDetalhesLoja,
    modalNovaLoja: ModalNovaLoja,
    modalCancelaLoja: ModalCancelaLoja,
  },

  methods: {

    resetComponents() {
      this.showInfo = false;
      this.showLoja = false;
    },

    menuSelecionado(opcao) {
      // Remover todas as classes 'selecionado' 
      $(".navegacao_item").removeClass("selected");
      $(`#${opcao}`).addClass("selected");

      this.resetComponents();

      switch (opcao) {
        case 'info':
          this.showInfo = true;
        break;

        case 'lojas':
          this.showLoja = true;
        break;

        default:
          alert("Em desenvolvimento!")
        break;
      }

    }
  }

};
</script>

<style scoped>

  /* Modal */
  #modal_info_loja, #modal_nova_loja, #modal_cancela_loja {
    display: none;
  }

  .conteudo_dinamico {
    background-color: var(--fundo-white);
    padding: 1rem;
  }
</style>


