<template>
	<div>
		<ul class="navegacao_header">
			<slot>
				<!-- Itens da barra de navegao -->
			</slot>
		</ul>
	</div>
</template>

<script>
	export default {

	};
</script>

<style scoped>
	.navegacao_header {
		display: flex;
		background-color: var(--fundo-white);
		border-radius: 0.25rem;
		box-shadow: 0 1px 2px 0 rgba(0,0,0,0.2);

		margin-bottom: 1rem;
	}

	.navegacao_item {
		padding: 1rem;
		transition: filter 0.3s;
		cursor: pointer;
		width: 100%;
		text-align: center;
	}

	.navegacao_item:hover {
		border-bottom: 1px solid var(--laranja);
	}

	.navegacao_item:active {
		background-color: var(--fundo);
	}

	.selected {
		background-color: var(--fundo);
		border-bottom: 1px solid var(--laranja);
	}
</style>


