import Home         from './views/Home.vue';
import Login        from './views/Login.vue';
import InfoCliente  from './views/InfoCliente.vue';

export const routes = [
	{
    path: '',
    component: Home
  },
  {
    path: '/login',
    component: Login
  },
  {
    path: '/cliente/:idCliente?',
    component: InfoCliente
  }

];



