<template>
	<!-- Input personalizado -->
	<div class="vr_input">
		<fieldset>
			<legend>{{ titulo }}</legend>
			<input :type="type" :placeholder=placeholder :id=id :required=required>
		</fieldset>
	</div>
</template>

<script>
	export default {
		props: ['titulo', 'type', 'placeholder', 'id', 'required'],
	}
</script>

<style scoped>
  fieldset {
    display: block;
    margin-left: 2px;
    margin-right: 2px;
    padding-bottom: 0.725em;
    padding-left: 0.75em;
    padding-right: 0.75em;
    border: 1px solid var(--borda-input);
    border-radius: 0.25rem;
  }

  legend {
    display: block;
    width: auto;
    max-width: 100%;
    padding-left: 0.3rem;
    padding-right: 0.35rem;
    margin-bottom: .5rem;
    font-size: 1rem;
    line-height: inherit;
    color: inherit;
    white-space: normal;
    color: var(--borda-input);
  }

	.vr_input > fieldset > input {
    border: none;
    outline: none;
    width: 100%;
    font-weight: bold;
  }

  .vr_input > fieldset {
    font-size: 1rem;
  }

</style>


