<template>
	<!-- Input personalizado -->
	<div class="vr_input" @click="setFocus(id)">
		<fieldset style="display: flex;">
			<legend>{{ titulo }}</legend>
			<input type="text"   @change="setColor(id)" placeholder="#000000" :id='"cor_"+id' maxlength="7">
			<input type="color"  @change="pickColor(id)" :id="'pick_color_'+id">
		</fieldset>
	</div>
</template>

<script>
	export default {
		props: ['titulo', 'type', 'placeholder', 'id', 'required'],

		methods: {
			setFocus(id) {
				$(`#cor_${id}`).focus();
			},

			setColor(id) {
				let cor = $(`#cor_${id}`).val();
				if((/^#[0-9A-F]{6}$/i).test(cor))
					$(`#pick_color_${id}`).val(cor);
				else {
					alert("Cor invlida!");
					$(`#cor_${id}`).val("");
				}
			},

			pickColor(id) {
				let cor = $(`#pick_color_${id}`).val();
				$(`#cor_${id}`).val(cor);
			}
		}
	}
</script>

<style scoped>
  fieldset {
	justify-content: space-between;
	align-items: center;
	display: block;
    margin-left: 2px;
    margin-right: 2px;
    padding-bottom: 0.725em;
    padding-left: 0.75em;
    padding-right: 0.75em;
    border: 1px solid var(--borda-input);
    border-radius: 0.25rem;
  }

  legend {
    display: block;
    width: auto;
    max-width: 100%;
    padding-left: 0.3rem;
    padding-right: 0.35rem;
    margin-bottom: .5rem;
    font-size: 1rem;
    line-height: inherit;
    color: inherit;
    white-space: normal;
    color: var(--borda-input);
  }

  .vr_input > fieldset > input {
    border: none;
    outline: none;
    font-weight: bold;
  }

  .vr_input > fieldset {
    font-size: 1rem;
  }

</style>


