<template>
  <div class="fundo">
    <div class="content">
      <div class="title">
        <img src="../assets/image/imgLogin.jpg" alt="imgLogin">
      </div>
      <div class="body">
        <inputDefault type="text" class="input" titulo="Email" id="email" required="true"/>
        <inputDefault type="password" class="input" titulo="Senha" id="senha" required="true"/>
        <button class="button_Login" @click="validaLogin">Login</button>
      </div>
    </div>
  </div>
</template>

<script>
  import InputDefault from '../components/shared/input/InputDefault.vue';

  export default {
    
    components: 
    {
      inputDefault: InputDefault
    },

    methods: {
      validaLogin() {
        if(($("#email").val() == "vradmin") & ($("#senha").val() == "vradmin12"))
        {
          this.$toast.success("Login realizado com sucesso!", {
            duration: 3000,
            position: "top-right"
          });
          this.$router.push({ path: `/` });
        }
        else
        {
          this.$toast.error("Erro ao tentar logar!", {
            duration: 3000,
            position: "top-right"
          });
        }
      }
    }


  }
</script>

<style scoped>
  .fundo {
    background-color: var(--fundo);
    height: 100%;
    width: 100%;
    display: inline-block;
    position: absolute;
  }

  .content { 
    background-color: white;
    justify-content: center;
    position: absolute;
    top: 15%;
    left: 40%;
    margin-left: -100px; 
    border-radius: 1.4rem;
    width: 35rem;
    height: 32rem;
  }

  .title {
    text-align: center;
    margin-top: 2rem;
  }

  .body {
    background-color: white;
    justify-content: center;
    padding: 1.5rem;
  }

  .input {
    margin-top: 3rem;
  }

  .button_Login {
    color: white;
    background-color: #1792ef;
    border-radius: 0.2rem;
    border: none;
    width: 100%;
    padding: 1.2rem;
    margin-top: 5rem;
    font-size: 1rem;
    cursor: pointer;
  }
</style>


