<template>
  	<!-- Input personalizado -->
	<div class="vr_input">
		<fieldset>

			<div class="apresentacao_img">
        <label id="label_file_img" title="Insira o logo" for="input_file_img"><i class="fas fa-upload" id="upload_icon"/></label>
				<img id="img_logo" src='' onerror="this.style.display='none'" title="Clique para alterar a imagem" onclick="$('#input_file_img').click();" />
        <p :id="'show_img_b64_'+id" style="display:none"></p>
			</div>

			<div class="input_img">
				<input type="file" id="input_file_img" :required=required @change="iniciaProcessoUpload(id)">
			</div>
		</fieldset>
	</div>
</template>

<script>
  export default {
    
    props: ['titulo', 'id', 'required', "height_limite", "width_limite"],

    methods: {
      // Step Inicial para o processo de registro da Imagem
      iniciaProcessoUpload(id) {
        // Seleciona a imagem subido pelo usurio
        let input = document.getElementById(`input_file_img`);
        let file = input.files[0];
        // Obtem a extenso do file
        let extensao = file.name.split('.').pop();

        try {
          if (extensao != "jpg")
            throw new Error("Extenso invlida da imagem!"); 
            
          // Processo de upload da imagem ao input file e ao 'src' respectivo
          let reader = new FileReader();

          reader.onload = function(e) {
						// Apenas ir sumir com o incone de Upload
        		$(`#label_file_img`).css('display', 'none');
						// Sobe a apresentaso da imagem
            $(`#img_logo`).attr("src", e.target.result);
            $(`#img_logo`).css("display", "block");
            $(`#show_img_b64_${id}`).html(e.target.result);
          };

          reader.onerror = function(error) {
            alert(error);
          };
          reader.readAsDataURL(file);

        } 
        catch (error) {
					alert("Extenso invlida da imagem!")
        }
      },

    }
  }
</script>

<style scoped>
 fieldset {
    display: block;
    margin-left: 2px;
    margin-right: 2px;
    padding-bottom: 0.725em;
    padding-right: 0.75em;
    border: none;
    border-radius: 0.25rem;
  }

  legend {
    display: block;
    width: auto;
    max-width: 100%;
    padding-left: 0.3rem;
    padding-right: 0.35rem;
    margin-bottom: .5rem;
    font-size: 1rem;
    line-height: inherit;
    color: inherit;
    white-space: normal;
    color:  var(--borda-input);
  }
 
	#label_file_img {
		cursor: pointer;
		border: 1px solid  var(--borda-input);
    padding: 1.5rem;
    border-radius: 0.25rem;
	}

  #input_file_img {
		display: none;
	}

	#img_logo {
		width: 100%;
		cursor: pointer;
		border-radius: 0.25rem;
		transition: filter 0.7s;
	}

	#img_logo:hover{
		filter: brightness(0.5);
	}

	.vr_input > fieldset {
    font-size: 1rem;
  }

	.input_img {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	.input_img > input {
    border: none;
    outline: none;
    font-weight: bold;
  }

	.input_img > p, .input_img > span {
    font-weight: bold;
  }

	.apresentacao_img {
		display: flex;
		align-content: center;
		justify-content: start;
		max-width: 5rem;
		max-height: 5rem;
	}

</style>


