<template>
	<div class="container_search">
		<span  class="icon"><i class="fa fa-search"></i></span>
		<slot>
			
		</slot>
	</div>
</template>

<script>
	export default {

	};
</script>

<style scoped>
	.box {
		margin-left: 1rem;
	}

	.container_search {
		vertical-align: middle;
		white-space: nowrap;
		position: relative;
		margin-right: 1.5rem;
	}

	.container_search > input {
		width: 100%;
		height: 30px;
		border: none;
		font-size: 10pt;
		float: left;
		color: #63717f;
		padding-left: 45px;
		-webkit-border-radius: 5px;
		-moz-border-radius: 5px;
		border-radius: 5px;

		box-shadow: 0 1px 2px 0 rgba(0,0,0,0.2);

		-webkit-transition: background 0.55s ease;
		-moz-transition: background 0.55s ease;
		-ms-transition: background 0.55s ease;
		-o-transition: background 0.55s ease;
		transition: background 0.55s ease;
	}

	.container_search > input::-webkit-input-placeholder {
		color: #65737e;
	}

	.container_search > input:-moz-placeholder {
		/* Firefox 18- */
		color: #65737e;
	}

	.container_search > input::-moz-placeholder {
		/* Firefox 19+ */
		color: #65737e;
	}

	.container_search > input:-ms-input-placeholder {
		color: #65737e;
	}

	.container_search .icon {
		position: absolute;
    	top: 24%;
    	margin-left: 7px;
		color: #4f5b66;
	}

	.container_search > input:focus,
	.container_search > input:active {
		outline: none;
	}
</style>


