<template>
  <div class="fundo_modal">
    <div class="my_modal">
      <div class="modal_header">
        <span class="title">Tem certeza que deseja cancelar esta loja?</span>
        <hr />
      </div>
      <div class="modal_body">

        <div class="flex">
          <img width="100" height="100" src="https://lh5.googleusercontent.com/p/AF1QipMAp-c01kHcLEwO8KGmc235XtGM1iTC_b0hqELV=w1080-k-no" alt="Loja">
          <div class="modal_detalhes_loja">
            <span>Loja</span>
            <p>OS. 00.000.000/0000-00</p>
            <p>ID: 00</p>
          </div>
        </div>

        <form>
					<inputTextArea titulo="Motivo" placeholder="Descreva o motivo do cancelamento" id="motivo_cancelamento" cols="40" rows="5" required="true"/>
          
          <div class="modal_footer">

            <b-button variant="outline-warning">Tags</b-button>

            <div class="btn_submit">
              <b-button variant="danger" @click="closeModal">No</b-button>
              <b-button type="submit" variant="success">Sim</b-button>
            </div>
          </div>
				</form>

      </div>
    </div>
  </div>
</template>

<script>

  import InputTextArea from '../../input/InputTextArea.vue';
 
	export default {
		components: {
      inputTextArea: InputTextArea
    },

		methods: {
			// Fecha o modal
			closeModal() {
				$(`.fundo_modal`).css( "display", "none");
			}
		}
	}
</script>

<style scoped>

  .modal_header > span {
		text-align: center;
    font-weight: bold;
  }

	.modal_body {
		margin-top: 2rem;
	}

  .modal_detalhes_loja {
    display: flex;
    flex-direction: column;

    gap: 1rem;
  }

  .modal_detalhes_loja > span {
    font-weight: bold;
  }

  .flex {
    display: flex;
    gap: 1rem;
    margin-bottom: 1rem;
  }

  .modal_footer {
    margin-top: 1rem;

    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .btn_submit {
    float: right;
  }

  .btn_submit > button {
    padding: 0.2rem 1.5rem;
  }

</style>


