<template>
  <b-navbar toggleable class="navbar">
    <b-navbar-brand href="/"><img src="/image/logo.png" /></b-navbar-brand>
      <div class="user_info">

        <div class="user_notifications">
          <i class="far fa-bell"></i>
        </div>

        <div class="user_info">
          <img  class="img_user" src="https://infantv.com.br/infantv/wp-content/uploads/2016/06/heman_gorpo.jpg" alt="Usuario">
          <span class="name_user">Rodrigo Santiago</span>
          <span class="drop_user"></span>
        </div>

      </div>
  </b-navbar>
</template>

<script>

  export default {

  }

</script>

<style scoped>
  .navbar {
    display: flex;
    grid-row: 1/2;
    grid-column: 1/3;
    position: relative;
    background-color: var(--primary);
  }

  .user_info {
    display: flex;
    align-items: center;
    cursor: default;
  }

  .user_info > .user_notifications {
    color: #fff;
    margin: 0 2rem;
  }

  .img_user {
    width: 30px;
    height: 30px;
    border-radius: 5rem;
  }

  .img_user, .name_user, .drop_user {
    color: #fff;
    margin: 0 0.5rem;
  }

  .drop_user {
    font-size: 10px;
    cursor: pointer;
  }

</style>


